
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Consolas;
    color: #fff;
}

.header {
    display: flex;
    justify-content: center;
    margin:20px;
}

.coin_logo {
    width: 200px;
    padding: 20px;
}
html, body {
    padding: 0;
    margin: 0 auto;
    font-family: Consolas;
    max-width: 1000px;
    background-color: #101012;
}

a {
  color: inherit;
  text-decoration: none;
}

.coin_app {
  display: flex;
  width: 1000px;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  color: #fff;
}

.controls {
    display: inline-block;
    margin-bottom: 20px;
    justify-content: space-between;
    width: 1000px;
    padding: 25px;
}

#loading {
    font-size: 12px;
}

@media (max-width: 900px) {
    
    .controls {
        width: 280px;
        margin: 25px,0,25px,0;
        display: inline-block;
        padding: 0;
    }

    body{
        max-width:320px;
    }

}
